import React, { createContext, FC, memo, ReactNode } from 'react';

export const AnalyticsGroupIdContext = createContext<string | null>(null);

export interface AnalyticsGroupProviderProps {
    id: string;
    children: ReactNode;
}

const AnalyticsGroupProvider: FC<AnalyticsGroupProviderProps> = memo(
    ({ id, children }) => {
        return (
            <AnalyticsGroupIdContext.Provider value={id}>
                {children}
            </AnalyticsGroupIdContext.Provider>
        );
    }
);

AnalyticsGroupProvider.displayName = 'AnalyticsGroupProvider';

export default AnalyticsGroupProvider;
